import React from 'react'
import Main from '../template/Main'

export default function Home(){
    return(
        <Main icon='fa-home' title='Incio'
            subtitle='Projeto CRUD desenvolvido em React.'
        >
            <h2 className='display-4'>Bem vindo!</h2>
            <hr/>
            <p className='mb-0'>
                Este site foi criado para treinamento do autor durante seus estudos em React.
                Caso esteja vendo esse site a partir do Github Pages, note que no haver conexo
                com servidor para o cadastro/recuperao de usurios. Clone e hospede a aplicao
                disponvel <a href='https://github.com/Miodini/projetos-curso-web/tree/main/crud-react'>aqui</a> localmente  .
            </p>
        </Main>
    )
}